@import '../colors';

.contactUsContent {
    & div {
        flex-wrap: nowrap;
    }
}

.contactUsBody {
    width: screen;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    .form {
        position: absolute;
        background-color: white;
        border: 1px solid $main;
        z-index: 10px;
        padding: 2rem;
        width: 60%;
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;
        border-radius: 0.5rem;
        @media screen and (max-width: 800px) {
            width: 80%;
            padding: 1rem;
        }
        .input {
            width: 53%;
            @media screen and (max-width: 800px) {
                width: 100%;
            }
        }
        .error {
            margin-top: 1rem;
            margin-bottom: 0rem;
            color: red;
        }
        h1 {
            font-size: 3rem;
            margin-bottom: 1rem;
            -webkit-text-fill-color: $grey;
            font-family: 'Kraftan', sans-serif;
            @media screen and (max-width: 800px) {
                font-size: 2rem;
            }
        }
        p {
            color: $main;
            margin-top: 0.25rem;
            font-family: 'Maven Pro', sans-serif;
            font-weight: 200;
            font-size: 1.25rem;
            @media screen and (max-width: 800px) {
                font-size: 0.75rem;
            }
        }
        input {
            width: 100%;
            padding: 1rem;
            margin-top: 1rem;
            color: $main;
            border: 1px solid $main;
            border-radius: 0.25rem;
            @media screen and (max-width: 800px) {
                width: 100%;
            }
        }
        input::placeholder {
            color: $main;
            opacity: 0.5;
        }
        textarea {
            width: 50%;
            height: 10rem;
            padding: 1rem;
            margin-top: 1rem;
            color: $main;
            border: 1px solid $main;
            border-radius: 0.25rem;
            @media screen and (max-width: 800px) {
                width: 90%;
            }
        }
        textarea::placeholder {
            color: $main;
            opacity: 0.5;
        }
    }
    img {
        z-index: 0;
        height: 45rem;
    }
}

.socialMediaBox {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    color: $main;
    
    & a {
        width: 42%;
        font-size: 3rem;
        color: inherit;
    }
}
